import { Component, OnInit } from '@angular/core';
import { MainTabService } from 'src/app/shared/main-tab.service';
import { Router, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-main-content',
  templateUrl: './main-content.component.html',
  styleUrls: ['./main-content.component.scss']
})
export class MainContentComponent implements OnInit {
  tabs = [];
  activeTabUrl: string;
  currentJustify="start";

  constructor(
    private tabService: MainTabService,
    private router: Router
  ) { }

  ngOnInit() {debugger
    this.tabs = this.tabService.tabs;
    this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {debugger
        this.activeTabUrl = event.urlAfterRedirects;
        if (this.tabs.length === 0) {
          this.tabService.addTab("",this.activeTabUrl);
        }
      }
    });
  }

  closeTab(index: number, event: Event) {debugger
    this.tabService.deleteTab(index);
    let url=this.activeTabUrl;
    let len=this.tabs.length;
    if (!this.tabs.find(tab => tab.url === url) && len>=1) {
      url=this.tabs[len-1].url;
    }else if (index===0 && len===2) {
      url=this.tabs[index+1].url;
    }else if (len===0 && !this.tabs.find(tab => tab.url === url)) {
      url="/dashboard/analytics";
    }
    this.router.navigateByUrl(url);
    event.preventDefault();
  }

  onTabChange(event) {debugger
    this.router.navigateByUrl(event.nextId);
  }

}
