import { Injector } from '@angular/core';
import { PermissionCheckerService } from '../permissions/permission-checker.service';
import { ToastrService } from 'ngx-toastr';
import MessageService from 'sweetalert2';
//import * as jwt_decode from 'jwt-decode';
import { NgbDateStruct, NgbCalendar, NgbTimeStruct } from '@ng-bootstrap/ng-bootstrap';
import { MaskService } from 'ngx-mask';
import { environment } from 'src/environments/environment';
import { UserDto } from '../dtos/auth/user-dto';
import { debounceTime, distinctUntilChanged, filter, map} from 'rxjs/operators';
import { Observable, Subject,merge, OperatorFunction } from 'rxjs';
import { ITypeheadFormatter } from '../interface/identity/typehead-formatter-interface';

export abstract class AppComponentBase {
    
    permission: PermissionCheckerService;
    mask: MaskService;
    notify: ToastrService; 
    message= MessageService;
    calendar:NgbCalendar;
    currentTime:string;
    appSession: UserDto=new UserDto();
    private readonly meridian = true;
    private readonly dt=new Date();
    tableDiv: boolean=true;
    baseUrl=environment.baseUrl;
    focus$ = [];
    click$ = [];
    typeheadlist = [];
    typeheadFormatter = (format: ITypeheadFormatter) => format.name;
    constructor(
      injector: Injector
      ) {
        this.permission = injector.get(PermissionCheckerService);
        this.notify = injector.get(ToastrService);
        this.appSession=this.UserSession();
        this.calendar=injector.get(NgbCalendar);
        this.currentTime=this.dt.getHours()+":"+this.dt.getMinutes()+":"+this.dt.getSeconds();
    }

    shTableDiv(div:string):boolean{
      if (div=="List") {
        this.tableDiv=true;
      } else {
        this.tableDiv=false;
      }
      return this.tableDiv;
    }

    private readonly DELIMITER = '-';
    
    parseNgbDate(value: string): NgbDateStruct | null {
      if (value) {
        let date = value.split(this.DELIMITER);
        return {
          year : parseInt(date[0], 10),
          month : parseInt(date[1], 10),
          day : parseInt(date[2], 10)
        };
      }
      return null;
    }
  
    formatNgbDate(date: NgbDateStruct | null): string {
      return date ?  date.year + this.DELIMITER + `${pad(date.month)}` + this.DELIMITER + `${pad(date.day)}` : '';
    }

    parseNgbTime(value: string| null): NgbTimeStruct | null {
        if (!value) {
          return null;
        }
        let dte;
        if(value.match(/T/g)){
          dte=new Date(value);
        }else{
          dte=new Date(new Date().toISOString().slice(0,10) + " " + value);
        }
        return {
          hour: dte.getHours(),
          minute: dte.getMinutes(),
          second: dte.getSeconds()
        };
      }
    
      formatNgbTime(time: NgbTimeStruct | null): Date | null {
        let timeString= time != null ? `${pad(time.hour)}:${pad(time.minute)}:${pad(time.second)}` : null;
        return new Date(new Date().toISOString().slice(0,10) + " " + timeString);
      }

      dateTimeString(date:NgbDateStruct,time:NgbTimeStruct):string{
        let dateString=this.formatNgbDate(date);
        let timeString= time != null ? `${pad(time.hour)}:${pad(time.minute)}:${pad(time.second)}` : null;
        return dateString+"T"+timeString;
      }

    isGranted(permissionName: string): boolean {
        return this.permission.isGranted(permissionName);
    }

    isGrantedAny(...permissions: string[]): boolean {
        if (!permissions) {
            return false;
        }

        for (const permission of permissions) {
            if (this.isGranted(permission)) {
                return true;
            }
        }

        return false;
    }

    UserSession():UserDto{debugger
        var token=localStorage.getItem("token");
        var user= localStorage.getItem("user");

        if(token){
            this.appSession = JSON.parse(user);
        }
        return this.appSession
    }
    searchType(index,instance):OperatorFunction<string, readonly {}[]> {
      if (this.focus$.length <= index) {
        this.focus$.push(new Subject<string>());
        this.click$.push(new Subject<string>());
      }
      return (text$: Observable<string>) => {
        const debouncedText$ = text$.pipe(
          debounceTime(200),
          distinctUntilChanged()
        );
        const clicksWithClosedPopup$ = this.click$[index].pipe(
          filter(() => !instance.isPopupOpen())
        );
        const inputFocus$ = this.focus$[index];
        return merge(debouncedText$, inputFocus$, clicksWithClosedPopup$).pipe(
          map(term =>
            (term === ""
              ? this.typeheadlist
              : this.typeheadlist.filter(
                  v => v.toLowerCase().indexOf(term.toLowerCase()) > -1
                )
            ).slice(0, 10)
          )
        );
      };
    }
  
}

const pad = (i: number): string => i < 10 ? `0${i}` : `${i}`;
