import { Injectable } from '@angular/core';
import { IMainTab } from './main-tab-interface';

@Injectable({
  providedIn: 'root'
})
export class MainTabService {

  tabs: IMainTab[] = [];

  constructor() { }

  addTab(name: string,url: string) {
    var tabObj={
      name:name?name:"Dashboard",
      url:url
    };
    const tab = this.getTabOptionByUrl(url);
    if (!this.tabs.includes(tab)) {
      this.tabs.push(tabObj);
    }
  }
   
  getTabOptionByUrl(url: string): IMainTab {
    return this.tabs.find(tab => tab.url === url);
  }
   
  deleteTab(index: number) {
    this.tabs.splice(index, 1);
  }

}
